  font-family: Arial, Helvetica, sans-serif;
  overflow: hidden;
}
.main-container{
  max-width: 768px;
  background: #eee;
  margin: 50px auto;
  height: 80vh;
  -webkit-box-shadow: 2px 2px 20px 1px rgba(0, 0, 0, .5);
