}
.chat__content{
  font-weight: 100;
  font-size: .9rem;
  line-height: 1.2rem;
  height: 70vh;
}
.content:not(:last-child),
.content:first-child{
  margin-top: .5rem;
}
.content-left{
  margin-left: .5rem;
  cursor: pointer;
  -webkit-transition: all .5s;
  transition: all .5s;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.content-left .bot{
  background: darkblue;
  display: inline-block;
  padding: 1rem;
  color: #fff;
  border-radius: 100%;
  margin-right: .5rem;
  text-transform: uppercase;
  -ms-flex-item-align: start;
      align-self: flex-start;
}
.content-left .bot-box{
  background: #fff;
  -webkit-box-shadow: 2px 2px 20px 1px rgba(0, 0, 0, .5);
          box-shadow: 2px 2px 20px 1px rgba(0, 0, 0, .5);
  display: inline-block;
  padding: 1rem;
  border-radius: .5rem;
  max-width: 40%;
}
.content-left:hover{
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.content-right{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-transition: all .5s;
  transition: all .5s;
  text-align: end;
}
.content-right .user{
  background: darkblue;
  padding: 1rem;
  color: #fff;
  border-radius: 100%;
  margin: 0 .5rem;
  text-transform: uppercase;
  display: inline-block;
  -ms-flex-item-align: start;
      align-self: flex-start;
}
.content-right .user-text{
  background: #fff;
  -webkit-box-shadow: 2px 2px 20px 1px rgba(0, 0, 0, .5);
          box-shadow: 2px 2px 20px 1px rgba(0, 0, 0, .5);
  display: inline-block;
  padding: 1rem;
  border-radius: .5rem;
  max-width: 40%;
}
.content-right:hover{
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.checker__radio{
  display: block;
}
.btn__accept,
.btn__reject{
  background: #333;
  color: #fff;
  padding: .5rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 1rem;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-transition: all .5s;
  transition: all .5s;
}
.btn__accept:hover,
.btn__reject:hover{
  background: #fff;
  color: #333;
